.resume-download {
  padding: 2rem 0;
  font-style: italic;
  line-height: 2rem;
  font-size: 0.875rem; }
  .resume-download a {
    font-size: 1.7rem;
    padding: 0 0.625rem;
    opacity: 1;
    display: inline-block;
    vertical-align: bottom; }

@-webkit-keyframes horizontal {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  6% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  12% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  18% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  24% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  30% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  36% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  42% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  48% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }

@keyframes horizontal {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  6% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  12% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  18% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  24% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  30% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  36% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  42% {
    -webkit-transform: translate(10px, 0);
            transform: translate(10px, 0); }
  48% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); } }
  .resume-download .faa-horizontal.animated,
  .resume-download .faa-horizontal.animated-hover:hover,
  .resume-download .faa-parent.animated-hover:hover > .faa-horizontal {
    -webkit-animation: horizontal 4s ease infinite;
            animation: horizontal 4s ease infinite; }



